@font-face {
  font-family: "thin-flat";
  src: url(../fonts/Roboto-Thin.ttf);
}

@font-face {
  font-family: "serif_";
  src: url(../fonts/BodoniModa-Italic-VariableFont_opsz\,wght.ttf);
}

@font-face {
  font-family: "pfont";
  src: url(../fonts/Montserrat-VariableFont_wght.ttf);
}

@font-face {
  font-family: "regular";
  src: url(../fonts/Roboto-Regular.ttf);
}

.background {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #dfdfdf;
  padding-right: 10%;
}

.grid-education {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 4fr;
      grid-template-columns: 1fr 4fr;
}

.grid-education .logo-place {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 50vh;
}

.grid-education .logo-place img {
  margin-top: 60px;
  width: 150px;
  height: 150px;
}

.grid-education .description {
  margin-top: 30px;
  margin-left: 50px;
}

.grid-education .description .subheading {
  font-family: 'serif_';
  font-size: 30px;
  margin-bottom: 15px;
}

.grid-education .description .wrapper div {
  font-family: 'regular';
  font-size: 13px;
  color: rgba(0, 0, 0, 0.5);
  letter-spacing: 1.3px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.5);
  margin-right: 20px;
  width: 150px;
  height: 30px;
  margin-bottom: 50px;
}

.grid-education .description .wrapper div:hover {
  color: #dfdfdf;
  background-color: rgba(0, 0, 0, 0.5);
}

.grid-education .description .wrapper-projects {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
}

.grid-education .description .wrapper-projects .imgHolder img {
  width: 80px;
}

.grid-education .description .wrapper-projects .projectHolder {
  margin-left: 40px;
}

.grid-education .description .wrapper-projects .projectHolder .language {
  font-size: 20px;
  font-family: 'pfont';
  color: rgba(0, 0, 0, 0.5);
  margin-bottom: 10px;
}

.grid-education .description .wrapper-projects .projectHolder .project-description {
  font-size: 18px;
  font-family: 'regular';
  color: rgba(0, 0, 0, 0.3);
}

@media screen and (max-width: 768px) {
  .grid-education {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr;
        grid-template-columns: 1fr;
  }
  .grid-education .logo-place {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 150px;
  }
  .grid-education .logo-place img {
    margin-top: 17px;
    width: 150px;
    height: 150px;
  }
  .grid-education .description {
    margin-top: 20px;
    margin-left: 0;
  }
}
